@tailwind base;
@tailwind components;
@tailwind utilities;

.header {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo {
    width: 100px;
}

.nav-items {
    padding: 0px 20px;
}

.nav-items>ul {
    display: flex;
    list-style-type: none;
}

.nav-items>ul>li {
    padding: 10px;
    margin: 10px;
}

.restaurant-container {
    display: flex;
    flex-wrap: wrap;

}

.restaurant-card {
    width: 200px;
    /* height: 300px; */
    padding: 10px;
    margin: 5px;
}

.login {
    margin-top: 15px;
    height: 30px;
    background-color: lightblue;
    border-radius: 4px;
}

.card-title {
    font-size: 18px;
    font-weight: 700;
    margin: 10px 0px;
    padding: 0px;
}

.card-body {
    font-size: 15px;
    margin-top: 0px;
    padding: 0px;
}

.offline {
    margin-top: 10px;
    color: red;
    background-color: lightgrey;
    padding: 2px;
    border-radius: 15px;
    font-weight: 500;
    text-align: center;
}

.card-body>p {
    margin: 10px 0px;
    padding: 0px;
}

.card-footer {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 500;
}

.restaurant-card:hover {
    cursor: pointer;
    border: 1px solid black
}

.res-img {
    width: 200px;
    height: 160px;
}

.search {
    padding: 10px;
}

.filter {
    display: flex;
    justify-content: start;
}

.count {
    margin-top: 10px;
}

.search-box {
    margin-right: 5px;
}

.filter-btn {
    margin: 10px;
}

.user-card {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid black;
}

.leetcode-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 16px;
    text-align: left;
  }
  
  .leetcode-table th,
  .leetcode-table td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .leetcode-table th {
    background-color: #f4f4f4;
    font-weight: bold;
  }
  
  .tag-chip {
    display: inline-block;
    background-color: #cff8e2;
    color: #2aa32e;
    padding: 5px 10px;
    margin: 2px;
    border-radius: 15px;
    font-size: 12px;
  }